.secciones {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 3 columnas en PC */
    gap: 30px;
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px 20px;
}

/* Ajuste de cada section */
.secciones section {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 12px;
    padding: 20px;
    backdrop-filter: blur(4px);
    transition: transform 0.3s ease;
}

.secciones section:hover {
    transform: translateY(-5px);
}

/* Contenedor general */
section {
    max-width: 900px;
    margin: 0 auto;
    padding: 40px 20px;
    color: #f0f0f0;
}

section h1 {
    font-size: 2.5rem;   
    margin-bottom: 15px;
    text-align: center;
    color: #1ce497; /* verde llamativo */
    text-shadow: 0px 2px 8px rgba(0, 0, 0, 0.7);
    border-bottom: 2px solid rgba(28, 228, 151, 0.6);
    display: inline-block;
    padding-bottom: 5px;
}

/* Párrafos */
section p {
    font-size: 1.5rem;     
    line-height: 1.6;    
    text-align: justify;
    margin-bottom: 30px;
    color: #ddd;
}

/* Lista de habilidades blandas */
section ul {
    list-style: none;
    padding: 0;
    margin: 10px 0 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
}

section ul li {
    background: rgba(255, 255, 255, 0.1);
    padding: 4px 8px;  
    border-radius: 8px;
    font-size: 2.3rem;  
    transition: all 0.3s ease;
}

section ul li:hover {
    background: rgb(28, 228, 151);
    color: #111;
    transform: scale(1.05);
}

/* Tecnologías */
.tecnologias {
    display: flex;
    gap: 25px;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
}

.tecnologias i {
    font-size: 4.5rem; 
    transition: transform 0.3s ease, filter 0.3s ease;
    cursor: pointer;
}

.tecnologias i:hover {
    transform: scale(1.2);
    filter: drop-shadow(0px 0px 10px rgba(28, 228, 151, 0.7));
}

/* 📱 Responsive */
@media (max-width: 992px) {
    .secciones {
        grid-template-columns: repeat(3, 1fr); /* 2 columnas en tablets */
    }
}

@media (max-width: 600px) {
    .secciones {
        grid-template-columns: 1fr; /* 1 columna en celular */
    }
}
